import Axios, { AxiosObservable } from 'axios-observable';
import { addressDTO, AddressType, CorreiosResponse } from '../models/util';
import { environment } from 'src/environments/environment';
import { Injectable, NgZone } from '@angular/core';
import { map } from 'rxjs/operators';
import { Observable, ReplaySubject, Subscription } from 'rxjs';
import { Router } from '@angular/router';
import { SystemService } from './system.service';
import { User } from '../models/user';
import {
  AlertController,
  LoadingController,
  MenuController,
  ModalController,
  NavController,
  Platform,
} from '@ionic/angular';
import { PokemonResponse } from '../models/pokemonResponse';
import data from './../../data/pokemons.json';

/**
 * Variveis e mtodos globais do sistema
 *
 * @example o usurio e o ReplaySubject que o controla  definido aqui
 */
@Injectable({
  providedIn: 'root',
})
export class SharedService {
  user: User; //auth user
  userSubscription: Subscription;
  user$: ReplaySubject<User> = new ReplaySubject<User>(1); //auth user observer

  constructor(
    public system: SystemService,
    public platform: Platform,
    public navCtrl: NavController,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    public menuCtrl: MenuController,
    public router: Router,
    public ngZone: NgZone,
    public modalCtrl: ModalController
  ) {
    this.listenUser();
  }

  /**
   * Atualizar usurio do sistema toda vez que ele alterar
   */
  listenUser() {
    this.userSubscription = this.user$.subscribe((user) => {
      console.log('Usurio atualizado: ', user);
      this.user = user;
      if (!this.user) {
        return this.handleLogoutUser();
      }
    });
  }

  handleLogoutUser() {
    this.userSubscription.unsubscribe();
  }

  getAddressByCep(cep): Observable<AddressType> {
    return Axios.get<AxiosObservable<CorreiosResponse>>(
      `https://viacep.com.br/ws/${cep}/json`
    ).pipe(
      map((res) => {
        if ((res.data as CorreiosResponse).erro === 'true') {
          return null;
        } else {
          return addressDTO(res.data as CorreiosResponse) as AddressType;
        }
      })
    );
  }

  getLocalPokemons(): PokemonResponse {
    return data;
  }

  getServerPokemons(targetUrl?) {
    targetUrl = targetUrl
      ? targetUrl
      : ' https://pokeapi.co/api/v2/pokemon/?limit=25';
    return Axios.get<PokemonResponse>(targetUrl).pipe(
      map((res) => res.data)
    );
  }
}
